.info_page {
  display: flex;
  color: white;
  flex: auto, 1;
}

.info_sections {
  display: flex;
  flex-direction: column;
}
.info_sections > a {
  padding: 40px 100px;
  text-align: center;
  font-size: x-large;
  border-width: 1px;
